import React, { PureComponent } from 'react'
import cx from 'classnames'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import * as actions from '~/actions'
import { mapDispatchToProps } from '~/types/action'
import * as G from '~/types/github'
import { Container, Dimmer, Segment, Header, Grid } from 'semantic-ui-react'
import RepoItem from '../RepoItem'
import { compose, map, addIndex } from 'ramda'
import styles from './index.css'

const COLUMN_NUM = 4
const idxMap = addIndex(map)

class RepoListPage extends PureComponent {
  static defaultProps = {
    className: ''
  }

  async componentDidMount() {
    const { actions } = this.props

    await actions.github.getRepos('g0v')
  }

  async componentDidUpdate() {
    const { actions, isLoading, repos } = this.props

    if (isLoading || repos.length !== 0) return

    await actions.github.getRepos('g0v')
  }

  render() {
    const { id, className, isLoading } = this.props
    const dimmed = isLoading
    let repos = this.props.repos
    if (dimmed && repos.length === 0) {
      repos = G.dummyRepoList
    }

    const rowCount = Math.floor(repos.length / COLUMN_NUM)
    const r = repos.length % COLUMN_NUM
    let as = []
    let i
    let j
    for (i = 0; i < rowCount; i++) {
      let xs = []
      for (j = 0; j < COLUMN_NUM; j++) {
        xs.push(repos[i * COLUMN_NUM + j])
      }
      as.push(xs)
    }
    j = i * COLUMN_NUM
    let bs = []
    for (i = 0; i < r; i++) {
      bs.push(repos[j + i])
    }
    if (bs.length) as.push(bs)

    return (
      <Dimmer.Dimmable className={styles.dimmable} as={Segment} blurring dimmed={dimmed}>
        <Dimmer active={dimmed}>
          <Header as='h2' inverted>
            Loading...
          </Header>
        </Dimmer>

        <Container id={id} className={cx(styles.main, className)}>
          <Grid columns={COLUMN_NUM}>{
            idxMap(
              (a, i) =>
                <Grid.Row key={i}>{
                  idxMap(
                    (aa, j) =>
                      <Grid.Column key={j}>
                        <RepoItem data={aa} />
                      </Grid.Column>,
                    a
                  )
                }</Grid.Row>,
              as
            )
          }</Grid>
        </Container>
      </Dimmer.Dimmable>
    )
  }
}

export default compose(
  withRouter,
  connect(
    state => {
      const isLoading = G.isRepoListLoading(state)
      const repos = G.getRepoList(state)

      return { isLoading, repos }
    },
    mapDispatchToProps(actions)
  )
)(RepoListPage)



