import React, { PureComponent } from 'react'
import cx from 'classnames'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import * as actions from '~/actions'
import { mapDispatchToProps } from '~/types/action'
import * as G from '~/types/github'
import { EmptyGroupStatus } from '~/types/metadata'
import { Statistic, List } from 'semantic-ui-react'
import MetadataStatistic from '~/components/MetadataStatistic'
import styles from './index.css'

class LandingPage extends PureComponent {
  static defaultProps = {
    className: '',
    status: EmptyGroupStatus
  }

  render() {
    const { id, className, status } = this.props

    return (
      <div id={id} className={cx(styles.main, className)}>
        <div className={styles.splash}>
          <div className={styles.splash__content}>
            <div className={styles.splash__brand}>
              <h1></h1>
              <p>Yet Another g0v Hub</p>
            </div>
            <Statistic.Group className={styles.status}>
              <Statistic inverted>
                <MetadataStatistic
                  side={200}
                  valid={status.valid}
                  invalid={status.invalid}
                  missing={status.missing}
                />
                <Statistic.Label>g0v.json</Statistic.Label>
              </Statistic>
              <Statistic inverted>
                <Statistic.Value className={styles.status__value}>2,000</Statistic.Value>
                <Statistic.Label>Logbot</Statistic.Label>
              </Statistic>
              <Statistic inverted>
                <Statistic.Value className={styles.status__value}>20</Statistic.Value>
                <Statistic.Label></Statistic.Label>
              </Statistic>
            </Statistic.Group>
          </div>
        </div>
        <div className={styles.content}>
          <ul className={styles.features}>
            <li>
              <h2></h2>
              <p> g0v.json <Link to="/groups"></Link></p>
            </li>
            <li>
              <h2></h2>
              <p></p>
              <p></p>
            </li>
            <li>
              <h2></h2>
              <p> JSON API </p>
            </li>
          </ul>
          <div className={styles.description}>
            <p><a href="https://grants.g0v.tw"> g0v </a><a href="https://grants.g0v.tw/projects/5969ed35d60a0d001ed1f7f6"> 2017 </a></p>
            <p></p>
            <ul>
              <li> Logbot </li>
              <li></li>
              <li></li>
              <li> g0v.json  g0v.json </li>
            </ul>
            <p> 2018 </p>
          </div>
          <div className={styles.footer}>
            <div className={styles.footer__content}>
              <h2></h2>
              <List>
                <List.Item>
                  <List.Icon name="github" />
                  <List.Content>
                    <a href="https://github.com/g0v/ambassador"></a>
                  </List.Content>
                </List.Item>
                <List.Item>
                  <List.Icon name="book" />
                  <List.Content>
                    
                  </List.Content>
                </List.Item>
              </List>
              <div className={styles.g0v}>
                powered by g0v
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default connect(
  state => {
    const status = G.getGroupStatus(state)

    return { status }
  },
  mapDispatchToProps(actions)
)(LandingPage)



